import "../assets/styles/styles.scss";
import "../assets/styles/article.scss";
import { OpenMessage } from '../assets/javascripts/message';
//import { reject } from "core-js/fn/promise";





const body = document.querySelector('body');
const suppcategory = document.querySelector(".suppcategory");


let calc;
let popUp;
let confirm;

const createFormCategory = () => {
    const newcategory = document.querySelector('.add-green');
    newcategory.addEventListener('click', async event => {
        event.stopPropagation();
        console.log("Bouton pressé");
        const result = await openPopUp();
    })
};

createFormCategory();

const createBackground = () => {
    calc = document.createElement('div');
    calc.classList.add('calc');
};

const createPopUp = () => {
    popUp = document.createElement('div');
    popUp.classList.add("popup");
    popUp.innerHTML = `
        <h1>Nouvel article</h1>
        <form action="/admin/createArticle" method="POST">
        <label for="name">Nom</label>
        <input name="name" type="text">
        <label for="description">Description</label>
        <textarea name="description"></textarea>
        <button>Enregistrer</button>
        </form>
    `;
    popUp.addEventListener("click", event => {
        event.stopPropagation();
    });
};

function openPopUp() {
    createBackground();
    createPopUp();
    calc.append(popUp);
    body.append(calc);

    return new Promise((resolve, reject) => {
        calc.addEventListener("click", () => {
          resolve(false);
          calc.remove();
        });
    });

}

// CREATING POP-UP SUPP CATEGORY



suppcategory.addEventListener('click', async () => {
    const result = await OpenMessage('Êtes-vous sur de vouloir supprimer cette article ?');
    if (result)
    {
        window.location.assign('./category.html');
    }
});


// MODIFICATION POP-UP

const modifFormCategory = () => {
    const modifcategory = document.querySelector('.modifcategory');
    modifcategory.addEventListener('click', async event => {
        event.stopPropagation();
        console.log("Bouton pressé");
        const result = await openModifPopUp();
    })
};

modifFormCategory();

